import React, { Component } from 'react';
import TopBar from './components/TopBar';
import FooterMenu from './components/FooterMenu';
import Content from './components/Content';
import Sidebar from './components/Sidebar';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      windowWidth: 0,
      windowHeight: 0,
    };
  }

  componentDidMount() {
    this.updateDimensions();
    window.addEventListener('resize', this.updateDimensions.bind(this));
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateDimensions.bind(this));
  }

  updateDimensions() {
    let windowWidth = typeof window !== 'undefined' ? window.innerWidth : 0;
    let windowHeight = typeof window !== 'undefined' ? window.innerHeight : 0;

    this.setState({ windowWidth, windowHeight });
  }

  render() {
    const { windowWidth } = this.state;

    const styles = {
      white: (opacity = 1) => `rgba(255, 255, 255, ${opacity})`,
      black: (opacity = 1) => `rgba(0, 0, 0, ${opacity})`,
      topBarHeight: 40,
      footerMenuHeight: 50,
      showFooterMenuText: windowWidth > 500,
      // showSidebar: windowWidth > 768,
      showSidebar: false,
      sidebarWidth: windowWidth < 1100 ? 50 : 150,
      sidebarCollapsed: windowWidth < 1100,
    };

    const menuItems = styles.showSidebar
      ? [
          // { icon: `üòÄ`, text: '1' },
          // { icon: `üòâ`, text: '2' },
          // { icon: `üòé`, text: '3' },
          // { icon: `ü§î`, text: '4' },
          // { icon: `üòõ`, text: '5' },
          // { icon: `üò∫Ô∏è`, text: 'Profile' },
          { icon: `‚öô`, text: 'Settings' },
        ]
      : [
          { icon: '', text: '1' },
          // { icon: `üòâ`, text: '2' },
          // { icon: `üòé`, text: '3' },
          // { icon: `ü§î`, text: '4' },
          // { icon: `üòõ`, text: '5' },
        ];

    return (
      <div
        style={{
          backgroundColor: styles.black(0.05),
          minHeight: '100vh',
          position: 'relative',
        }}
      >
        {styles.showSidebar ? <Sidebar menuItems={menuItems} styles={styles} /> : <TopBar styles={styles} />}

        <Content styles={styles} />

        {!styles.showSidebar && <FooterMenu menuItems={menuItems} styles={styles} />}
      </div>
    );
  }
}

export default App;
